<template>
    <div class="ui-main-module">
        <div class="ui-fn-module">
            <el-button type="text" @click="handleExportListInfo" size="small" style="margin-right:10px;">
                <i class="icon-daochu icon-main"></i>导出
            </el-button>
            <ComUpload btnType="text" label="批量导入" :url="importUrl" @success="handleUploadSuccess"
                       :upData="upData" :multiple="true">
                <template slot="icon" slot-scope="scope">
                    <i class="icon-daochu icon-main"></i>
                </template>
            </ComUpload>

            <el-button size="small" class="load-text" type="text" @click="handleDownloadModule">
                <i class="icon-daochu icon-main"></i>下载模板
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="delData">
                <i class="icon-daochu icon-main"></i>删除
            </el-button>
            <el-button type="text" class="danger-border" size="small" @click="goBack">
                <i class="icon-daochu icon-main"></i>返回
            </el-button>
        </div>
        <!--<div class="ui-fn-module operation-box">-->
        <!--<div>-->
        <!--<el-button type="primary" @click="handleExportListInfo" size="small">导出</el-button>-->
        <!--<ComUpload label="批量导入" :url="importUrl" @success="handleUploadSuccess"-->
        <!--:upData="upData" :multiple="true"></ComUpload>-->

        <!--<el-button size="small" class="load-text" type="text" @click="handleDownloadModule">下载模板</el-button>-->
        <!--</div>-->
        <!--<div>-->
        <!--<el-button type="danger" class="danger-border" size="small" @click="delData">删除</el-button>-->
        <!--<el-button type="primary" class="danger-border" size="small" @click="$router.go(-1)">返回</el-button>-->
        <!--</div>-->
        <!--</div>-->

        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item)"
                              :class="params.fid == item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>
            <template slot="remark" slot-scope="scope">
                <!--<el-tooltip placement="left">-->
                <!--<div class="el-tooltip-content" slot="content">{{scope.row.remark}}</div>-->
                <!--<div class="el-tooltip-left-more">{{scope.row.remark}}</div>-->
                <!--</el-tooltip>-->
            </template>
            <template slot="pack_img" slot-scope="scope">
                <div class="pack-img" :style="{'background':'url('+ scope.row.pack_img +') no-repeat 0/contain'}"
                     @click="handlePictureCardPreview(scope.row.pack_img)">
                </div>
            </template>
            <template slot="operate" slot-scope="scope">
                <el-table-column
                    fixed="right"
                    label="操作"
                    width="150px">
                    <template slot-scope="scope">
                        <el-button @click="delItem(scope.row.id)" type="text" size="small">
                            删除
                        </el-button>
                        <el-button type="text" @click="editItem(scope.row)" size="small">编辑</el-button>
                        <el-button type="text" @click="viewLog(scope.row.id)" size="small">日志</el-button>
                    </template>
                </el-table-column>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog title="编辑" class="ui-layout_edit-dialog" :visible.sync="dialogFormVisible"
                   :before-close="cancelEditInfo">
            <el-form :model="editform" label-width="120px" style="width: 90%;">
                <el-form-item :label="item" v-for="(item,index) in tableKey" :key="index">
                    <el-input v-if="!(tableProps[index]==='pack_img')"
                              v-model="editform[tableProps[index]]"
                              :disabled="tableProps[index]==='id'||tableProps[index]==='pack_type_id'||tableProps[index]==='create_time'||tableProps[index]==='create_user'"></el-input>
                    <div v-if="tableProps[index]==='pack_img'" style="text-align: left;">

                        <el-upload
                            action=""
                            :limit="1"
                            list-type="picture-card"
                            :on-success="handleAvatarSuccess"
                            :on-preview="handlePictureCardPreview"
                            :on-remove="handleRemove"
                            :on-exceed="handleExceed"
                            :file-list="editform.fileList">
                            <i class="el-icon-plus"></i>
                        </el-upload>
                    </div>
                    <!--<el-select style="width: 100%" v-if="tableProps[index]==='pack_type_id'"-->
                    <!--v-model="editform.pack_type_id"-->
                    <!--placeholder="请选择">-->
                    <!--<el-option-->
                    <!--v-for="(label, value) in packType"-->
                    <!--:key="value"-->
                    <!--:label="label"-->
                    <!--:value="value"></el-option>-->
                    <!--</el-select>-->
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消
                </el-button>
                <el-button type="primary" @click="saveEditInfo">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="查看日志" class="ui-layout_edit-dialog" :visible.sync="dialogTableVisible">
            <ComTableInfo
                :tableKey="logKey"
                :tableProps="logProps"
                :tableData="logData"
                :tableWidth="logWidth">
                <template slot="remark" slot-scope="scope">
                    <p v-for="(item,index) in scope.row.remark" :key="index">
                        {{item}}
                    </p>
                </template>
            </ComTableInfo>
        </el-dialog>
        <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import * as downloadApi from '@/api/download_api';
import {
    mapGetters
} from 'vuex';
import {
    getDeliveryPackListInfo,
    delDeliveryPack,
    changeDeliveryPackInfo,
    exportDeliveryPackInfo,
} from '@/api/quality_control_department/sample_quality_control/data_maintain/delivery_pack';
import { getLogList, upLoadPhoto } from '@/api/common';

export default {
    props: {
        queryParams: {}
    },
    data () {
        return {

            // 搜索缩进
            showSearch: true,

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],
            packType: {},

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 上传列表
            saveFileList: [],

            //编辑弹窗
            dialogFormVisible: false,
            editform: {},

            //日志弹窗
            dialogTableVisible: false,
            logData: [],
            logKey: [],
            logProps: [],
            logWidth: ['200px', '120px', '120px'],

            //导入的接口
            importUrl: '',
            upData: {},

            dialogImageUrl: '',
            dialogVisible: false,

            //列表tab选项
            moduleTypeList: [
                {
                    label: '发货包装',
                    value: '1',
                    log: '3'
                },
                {
                    label: '缓冲包装',
                    value: '2',
                    log: '3'
                },
                {
                    label: '退样类型',
                    value: '5',
                    log: '2'
                },
                {
                    label: '来料包装',
                    value: '3',
                    log: '3'
                },
                {
                    label: '质检等级',
                    value: '6',
                    log: '5'
                },
                {
                    label: '转接头',
                    value: '4',
                    log: '3'
                }
            ],

            params: {}

        };
    },
    computed: {
        ...mapGetters([
            'loginInfo',
            'queryParamsInfo'
        ]),
        downloadUrl () {
            return downloadApi['DOWNLOAD_PACK_MAINTAIN'];
        }
    },
    methods: {
        //根据id 处理不同的参数
        handleImport () {
            switch (this.params.fid) {
                case '5':
                    this.importUrl = '/sample_config/return_config/import_config';
                    break;
                case '6':
                    this.importUrl = '/sample_config/quality_config/import_config';
                    break;
                default:
                    this.importUrl = '/sample_config/sample_config/import_config';

            }
            this.upData = {
                user_id: this.loginInfo.staff_code || '12534',
                fid: this.params.fid
            };
        },
        handleExceed () {
            this.$message.warning('只能上传一张图片');
        },
        handleAvatarSuccess (res, file) {
            let obj = new FormData();
            obj.append('file', file.raw);
            upLoadPhoto(obj).then(({data}) => {

                if (data.status == 1) {
                    this.$set(this.editform, 'pack_img', data.data.path);
                } else {
                    this.$message.error(data.errorMess);
                }
            });
        },
        handleRemove (file, fileList) {
            this.editform.pack_img = '';
        },
        handlePictureCardPreview (file) {
            if (file.url) {
                this.dialogImageUrl = file.url;
            } else {
                this.dialogImageUrl = file;
            }
            this.dialogVisible = true;
        },
        // 切换tab列表
        handleToggleTableInfo (obj) {
            this.params.fid = obj.value;
            this.params.log = obj.log;
            this.handleQueryInfo();
            this.handleImport();
        },

        // 下载导入模板
        handleDownloadModule () {
            window.open(`${this.downloadUrl}?fid=${this.params.fid}`, '_self');
        },

        //上传文件成功
        handleUploadSuccess (res, file) {
            if (res.errorMess) {
                this.$message.error(res.errorMess || '操作失败');
            } else {
                this.handleQueryInfo();
            }
        },

        //查看日志
        viewLog (id) {
            this.dialogTableVisible = true;
            let obj = {
                mod_type: this.params.log,
                id
            };
            getLogList(obj).then(({data}) => {
                if (data.status === 1) {
                    this.logData = data.data.value;
                    this.logData.map((item) => {
                        return item.remark = item.remark.split('|');
                    });
                    this.logProps = Object.keys(data.data.key);
                    this.logKey = Object.values(data.data.key);
                } else {
                    this.logData = [];
                }
            });
        },

        //取消保存
        cancelEditInfo () {
            this.dialogFormVisible = false;
            this.editform.fileList = [];
        },

        //保存修改
        saveEditInfo () {

            let newData = {...this.editform};
            delete newData.create_time;
            delete newData.fileList;

            // 转换类型
            for (let i in this.packType) {
                if (newData.pack_type_id === this.packType[i]) {
                    newData.pack_type_id = i;
                    break;
                }
            }

            let obj = {
                fid: this.params.fid,
                user_id: this.loginInfo.uid,
                ...newData
            };
            changeDeliveryPackInfo(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.dialogFormVisible = false;

                this.$message({
                    message: '修改成功!',
                    type: 'success'
                });
                this.handleQueryInfo();
            });

        },

        //编辑弹窗
        editItem (obj) {

            this.dialogFormVisible = true;
            this.editform = Object.assign({}, obj);
            if (this.editform.pack_img !== '') {
                this.editform.fileList = [{
                    name: '',
                    url: this.editform.pack_img
                }];
            }

        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.id);
            });
            return idArr;
        },

        //删除多个
        delData () {
            let idArr = this.selectId();
            if (!idArr.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: idArr.join(','),
                    user_id: this.loginInfo.uid,
                    modify_user: this.loginInfo.user_name,
                    fid: this.params.fid
                };
                delDeliveryPack(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        //删除单个
        delItem (id) {
            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    id: id,
                    user_id: this.loginInfo.uid,
                    modify_user: this.loginInfo.user_name,
                    fid: this.params.fid
                };
                delDeliveryPack(obj).then(() => {
                    this.$message({
                        message: '删除成功!',
                        type: 'success'
                    });
                    this.handleQueryInfo();
                });
            });
        },

        // 导出明细信息
        handleExportListInfo () {
            let obj = {
                fid: this.params.fid,
                id: this.selectId().join(',')
            };
            exportDeliveryPackInfo(obj);
        },

        // 列表信息查询
        handleQueryInfo (params = {}) {
            console.log(this.params);
            let obj = {
                fid: this.params.fid,
                ...params
            };
            console.log(obj, 88);
            getDeliveryPackListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            });
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            });
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            //数据渲染
            this.tableData = data.data.value;
            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);

            this.packType = data.select_list.pack_type;

            this.tableWidth = ['', '', '', '', '220px', '220px'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },
        goBack () {
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'basic_data_router',
                value: null
            });
            this.$router.go(-1);
        }
    },
    created () {
        this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
            key: 'basic_data_router',
            value: this.$route.fullPath
        });
        let arr = this.queryParams.split(',');
        this.params = {
            fid: arr[0],
            log: arr[1]
        };

        this.handleQueryInfo();
        this.handleImport();
    }

};
</script>

<style lang="less" scoped>

@primary-color: #1299d8;

/deep/ .el-input {
    width: 100%;
}

.operation-box {
    display: flex;
    justify-content: space-between;

    > div {
        display: flex;
        align-items: center;

        .upload-demo {
            margin: 0 10px;
        }
    }

    .danger-border {
        background: #f5f5f5;
        color: #FF3333;
        border-color: #FF3333;
    }

    .load-text {
        color: @primary-color;
        text-decoration: underline;
        font-size: 13px;
        font-weight: bold;
    }
}

.pack-img {
    width: 100px;
    height: 100px;
}
</style>



