
/**  --->>>>   |^ 样品采购下单^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取样品采购下单信息
export function getSamplePurchaseInfo(queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        ...CONFIG.ajaxData,
        ...queryData
    };

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?';
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`;
        }
        window.open('/supplier/supplier/create_supplier' + str, '_self');
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            });
        }));
    } else {
        return http.getAjax('/sample/sample/select_sample_list', data);
    }
}

// 获取明细信息
export function getSamplePurchaseDetailInfo(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample/sample/sample_create_order_info', data);
}

// 提交生成订单
export function submitCreateOrder(params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample/sample/submit_sample_info', data);
}


